<script lang="ts">import { getContext } from 'svelte';
;
import { USER_ROLES } from '../bin/models/StaticTypes';
export let handleView;
const { state: { view, authenticated }, Methods: { logout }, } = getContext('state');
const handleLogout = () => {
    localStorage.clear();
    return logout(() => (authenticated.set(undefined), view.set('')));
};
</script>

<style>
  nav {
    background-color: #dbdbdb;
    box-sizing: border-box;
    border-top: 3px solid #eb637f;
    border-bottom: 3px solid #8b9eab;
    border-radius: 20px;
    font-weight: 300;
    padding: 0 1em;
    display: flex;
  }

  div {
    margin: auto 0;
    opacity: 0.7;
    cursor: pointer;
  }
  div.actions {
    position: absolute;
    top: 25px;
    right: 20px;
  }

  img {
    height: 40px;
    border-radius: 50px;
  }

  ul {
    margin: 0;
    padding: 0;
  }

  /* clearfix */
  ul::after {
    content: '';
    flex: 1;
    clear: both;
  }

  li {
    flex: 1;
    float: left;
    font-weight: 500;
    list-style: none;
    margin: 15px 5px;
    cursor: pointer;
  }

  .selected,
  li:hover,
  .actions span:hover {
    position: relative;
    display: inline-block;
    color: #ff4000ab;
  }

  .selected::after,
  li:hover::after,
  .actions span:hover::after {
    position: absolute;
    content: '';
    width: calc(100% - 1em);
    height: 4px;
    border-radius: 10px;
    background-color: #1c8806;
    display: block;
    bottom: -1px;
  }
</style>

<nav>
  <div id="" on:click={handleView}><img alt="brand" src={'favicon.jpg'} /></div>
  <ul>
    <li id={'posts'} on:click={handleView} class:selected={$view === 'posts'}>
      Posts
    </li>
    {#if $authenticated && $authenticated.role === USER_ROLES.admin}
      <li id={'users'} on:click={handleView} class:selected={$view === 'users'}>
        Users
      </li>
    {/if}
  </ul>
  <div class="actions">
    {#if !$authenticated}
      <span
        id={'login'}
        on:click={handleView}
        class:selected={$view === 'login'}>
        Login
      </span>
      <span
        id={'register'}
        on:click={handleView}
        class:selected={$view === 'register'}>
        Register
      </span>
    {:else}
      <span
        id={'profile'}
        on:click={handleView}
        class:selected={$view === 'profile'}>
        Profile
      </span>
      <span on:click={handleLogout}>Logout</span>
    {/if}
  </div>
</nav>
